export default {
  props: {
    item: {
      type: Object
    },
    show: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    goMap() {
      let that = this;
      console.log(that.item);
      uni.openLocation({
        latitude: parseFloat(that.item.latitude),
        longitude: parseFloat(that.item.longitude),
        name: that.item.shopName,
        address: that.item.address,
        success: function() {
          console.log("success");
        },
        fail(e) {
          console.log(e);
        }
      });
    },
	goShopDetails(e) {
		uni.navigateTo({
		  url: "shopDetail"
		});
	}
  }
};
